.plan {
    background: #F8FAFE;
    padding: 110px 0 90px;

    @media only screen and (max-width: 600px) {
        padding: 35px 0 0;
    }

    &__heading {
        @include nawabSectionTitle;
        margin-bottom: 60px;

        @media only screen and (max-width: 600px) {
            margin-bottom: 40px;
        }
    }

    &__content {
        display: flex;

        .plan-icon {
            width: 16px;
            height: 16px;
            background: rgba(238, 43, 71, 0.1);
            border-radius: 50%;
            margin-right: 15px;
            margin-top: 5px;
            color: $primamryColorRed;

            i {
                font-size: 12px;
                margin: 0px 7px;
            }
        }
    }

    &__title {
        @include serviceHeading;
        font-weight: 600;
    }

    &__dec {
        @include nawabSectionDescription;
        text-align: left;
    }

    &__default {
        @extend .plan-icon;
    }
}