.plan {
    background: #F8FAFE;
    padding: 110px 0 90px;

    @media only screen and (max-width: 600px) {
        padding: 50px 0 0px 0;
    }

    &__heading {
        @include nawabSectionTitle;
        margin-bottom: 59px;

        @media only screen and (max-width: 600px) {
            margin-bottom: 40px;
        }
    }

    &__content {
        display: flex;

        .plan-icon {
            width: 16px;
            height: 16px;
            background: rgba(238, 43, 71, 0.1);
            border-radius: 50%;
            margin-right: 10px;
            margin-top: 5px;
            color: $primamryColorRed;
        }
    }

    &__title {
        @include serviceHeading;
        font-weight: 600;
    }

    &__dec {
        @include nawabSectionDescription;
        text-align: left;
    }

    &__default {
        @extend .plan-icon;
    }
}